.Toolbar{
    z-index: 90;
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: bisque;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.Logo{
    height: 80%;
}

@media (max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}